import React, { useState } from "react";
import { dbService, storageService } from "fbase";
import { ref, uploadString, getDownloadURL } from "@firebase/storage";
import { v4 as uuidv4 } from 'uuid';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus, faTimes } from "@fortawesome/free-solid-svg-icons";

const NweetFactory = ({userObj}) => {

    const [nweet, setNweet] = useState();
    const [attachment, setAttachment] = useState("");
    
    const onChange = (event) => {
        const {target:{value}} = event; 
        setNweet(value);
    }

    // return Promise, so set Async & Await
    const onSubmit = async (event) => { 
        event.preventDefault();
        if (nweet === "") {
            return;
        }
        let attachmentURL = "";
        if(attachment !== "") {
            const fileRef = ref(storageService, `${userObj.uid}/${uuidv4()}`); // 1. íŒŒì¼ì— ëŒ€í•œ reference ê°–ê¸°
            const response = await uploadString(fileRef, attachment, "data_url"); // 2. í•´ë‹¹ refë¡œ bucketì— ì €ì¥í•˜ê¸°
            
            // [return] promise | promise : ê¸°ë‹¤ë ¤ë‹¬ë¼, await: ê¸°ë‹¤ë¦¬ê² ë‹¤ -> ê°’ì„ ë°›ì„ ë•Œê¹Œì§€ ê¸°ë‹¤ë¦¼ 
            // https://firebase.google.com/docs/reference/js/storage#getdownloadurl
            attachmentURL = await getDownloadURL(response.ref); // 3. í•´ë‹¹ refì˜ URL ë°›ê¸° (collectionì— ì£¼ì†Œ ì €ì¥) 
        }
        const nweetObj = {
            text:nweet,
            createdAt: Date.now(),
            creatorId: userObj.uid,
            attachmentURL,
        }
        await dbService.collection("nweets").add(nweetObj); // 4. collectionì— ì €ì¥.
        setNweet("");
        setAttachment("");
    }
    
    const onFileChange = (event) => {
        const {
          target: { files },
        } = event;
        const theFile = files[0];
        const reader = new FileReader();
        reader.onloadend = (finishedEvent) => {
          const {
            currentTarget: { result },
          } = finishedEvent;
          setAttachment(result);
        };
        reader.readAsDataURL(theFile);
      };

    const onClearAttachment = () => setAttachment("");

    return (
        <form onSubmit={onSubmit} className="factoryForm"> 
            <span style={{ color:"white", fontSize: 20, paddingBottom:10}}>{"ì•ˆë…•í•˜ì„¸ìš”?"}</span>
            <span style={{ color:"white", fontSize: 20, paddingBottom:10}}>{"ë°©ë¬¸ ì´ë ¥ì„ ë‚¨ê²¨ì£¼ì„¸ìš”! ğŸ˜"}</span>
            <span style={{ color:"white", fontSize: 18, paddingBottom:10}}>{"ëª¨ë“  ë‚´ìš©ì€ ìµëª…ìœ¼ë¡œ ì „ë‹¬ë©ë‹ˆë‹¤"}</span>
            <span style={{ color:"darkgray", fontSize: 11, paddingBottom:5}}>*ë¶€ì ì ˆí•œ ë‚´ìš©ì€ ê´€ë¦¬ìì— ì˜í•´ ì‚­ì œ ì¡°ì¹˜ë  ìˆ˜ ìˆìŠµë‹ˆë‹¤. </span>
            <span style={{ color:"darkgray", fontSize: 11, paddingBottom:20}}>*ë°°ë‹¤ë¡±ì´ ì œì‘ì— ì°¸ì—¬í•œ HeyNana ì–´í”Œë„ ë§ì´ ì´ìš© ë¶€íƒë“œë ¤ìš”! </span>
            <div className="factoryInput__container">
                <input
                className="factoryInput__input"
                value={nweet}
                onChange={onChange}
                type="text"
                placeholder="ë¬¸êµ¬ ì…ë ¥..."
                maxLength={120}
                style={{"textAlign":"center"}}
                />
                <input type="submit" value="&rarr;" className="factoryInput__arrow" />
            </div>
           
            <label htmlFor="attach-file" className="factoryInput__label">        
            <span>ì‚¬ì§„ ì—…ë¡œë“œ</span>
                <FontAwesomeIcon icon={faPlus} />
            </label>
            <input
            id="attach-file"
            type="file"
            accept="image/*"
            onChange={onFileChange}
            style={{
            opacity: 0,
            }}
            />
            {attachment &&  (
            <div className="factoryForm__attachment">
            <img
            alt="attachment"
            src={attachment}
            style={{
            backgroundImage: attachment,
            }}
            />
            <div className="factoryForm__clear" onClick={onClearAttachment}>
                <span>Remove</span>
                <FontAwesomeIcon icon={faTimes} />
            </div>
            </div>
            )}
        </form>
    )
}

export default NweetFactory;