import React, { useState } from "react";
import { createUserWithEmailAndPassword, signInWithEmailAndPassword } from 'firebase/auth';

const AuthForm = ({auth}) => {
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [newAccount, setNewAccount] = useState(true);
    const [error, setError] = useState("");

    const onChange = (event) => {
        const {target: {name, value}} = event;
        if(name === "email") setEmail(value);
        else if(name === "password") setPassword(value);
    }

    const onSubmit = async (event) => {
      event.preventDefault();
      try {
        if(newAccount) {
          await createUserWithEmailAndPassword(auth, email, password);
        } else {
          await signInWithEmailAndPassword(auth, email, password);
        }
      } catch(error) {
        //Firebase: Error (auth/email-already-in-use).
        if(error.message.includes('email-already-in-use')) setError("í•´ë‹¹ ê³„ì •ìœ¼ë¡œ ê°€ì…í•œ ì´ë ¥ì´ ì¡´ì¬í•©ë‹ˆë‹¤! ë¡œê·¸ì¸ í•´ì£¼ì„¸ìš”") 
        else if (error.message.includes('account-exists-with-different-credential')) setError("ë‹¤ë¥¸ ë°©ì‹ìœ¼ë¡œ ê°€ì…ëœ ê³„ì •ì…ë‹ˆë‹¤. í•´ë‹¹ ë°©ì‹ìœ¼ë¡œ ë¡œê·¸ì¸ í•´ì£¼ì„¸ìš”!")
        else setError(error.message)
      }
    }

    const toggleAccount = () => {
      setNewAccount((prev)  => !prev) // setNewAccount(!newAccount); 
    }

    return (
        <>
        <form onSubmit={onSubmit} className="container">
            <input
                className="authInput"
                name="email"
                type="email"
                placeholder="Email"
                required
                value={email}
                onChange={onChange} 
                style={{"textAlign":"center"}}/>
            <input
                className="authInput"
                name="password"
                type="password"
                placeholder="Password"
                required
                value={password}
                onChange={onChange} 
                style={{"textAlign":"center"}}/>
            <input
              type="submit"
              className="authInput authSubmit"
              value={newAccount ? "ê°€ì…í•˜ê¸°" : "ë¡œê·¸ì¸"}
            />
            {error && <span className="authError">{error}</span>}
            </form>
            <span onClick={toggleAccount} className="authSwitch">
              {newAccount ? "ê³„ì •ìœ¼ë¡œ ë¡œê·¸ì¸í•˜ê¸°" : "ì´ë©”ì¼ë¡œ ê°€ì…í•˜ê¸°"}
            </span>
      </>
    )
}

export default AuthForm;